import React, { Component } from 'react';

class BackgroundTextOverlay extends Component {
  render () {
    return (
      <div id="background-text-overlay">
        <span className="text-large">Kristina Timkova</span>
        <span className="text-medium">Full Stack Developer</span>
      </div>
    );
  }
}

export default BackgroundTextOverlay;



